<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>应用系统管理</div>
            <div class="buttonbox" v-show="!inforadmin">
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:app:add')">新增</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:app:')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:app:delete')">删除</button>
                <button type="button" class="btn btn-parimary" @click="distributeData" v-if="$api.opreate.hasLoginComponents(this,'sys:app:distribute')">功能分配</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:app:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin" :tableSelectData="tableSelectData"></tables>
            <!-- 应用系统添加 -->
            <div v-show="inforadmin">
                <div class="col-sm-6">
                    <h5>
                        <span v-if="opreate=='add'">应用系统添加</span>
                        <span v-else-if="opreate=='update'">应用系统修改</span>
                    </h5>
                </div>
                <form class="form form-horizontal col-sm-8">
                    <div class="form-group">
                        <label class="control-label col-sm-2">应用系统名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入应用系统名,最多为20位'" v-model="addData.name" :data-vv-as="'应用系统名'" v-validate="$api.opreate.validateAdd('name',20,stateid,opreate)">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">备注</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('remark')&&addData.remark!='','has-error':errors.has('remark')}">
                            <input type="text" class="form-control" name="remark" :placeholder="'请输入备注'" v-model="addData.remark" :data-vv-as="'备注'" v-validate="'required'">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('remark')">{{errors.first("remark")}}</span>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="control-label col-sm-2">详情描述</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('mydetail')&&addData.detail!='','has-error':errors.has('mydetail')}">
                            <textarea rows="6" class="form-control" name="mydetail" placeholder="请输入描述信息" v-model="addData.detail" data-vv-as="描述" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('mydetail')">{{errors.first("mydetail")}}</span>
                        </div>
                    </div>
                    <!-- 按钮 -->
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforadmin=false">返回</button>
                    </div>
                </form>
            </div>
            <!--功能分配-->
            <table-fun-all v-show="tablefunall" :tablefunall.sync="tablefunall" :state="'application'"></table-fun-all>
        </div>
    </div>
</div>
</template>

<script>
import tables from "@/components/table.vue";
import tableFunAll from "./gongneng/FunAllcation";
export default {

    data: function () {
        return {
            inforadmin: false,
            searchData: {
                data: ""
            },
            addData: {
                name: "",
                remark: "",
                detail: ""
            },
            opreate: "add", //默认添加操作
            tableSelectData: { //传递给表格的数据
                selectTableurl: "selectManageSystemPaging.action", //获取应用系统的url
                pageloginData: {
                    id: this.stateid //当前页面的url
                }
            },
            tablefunall: false, //是否显示功能分配
        };
    },
    props: { //上级组件传递数据
        stateid: String,
        statetitle: String
    },
    components: {
        tables,
        tableFunAll
    },
    methods: {
        //数据添加操作
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
            var bool;
            if (vm.opreate == "update") {
               bool = vm.$api.http.opreateHttp(vm, "updataSystemManage.action", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                 bool = vm.$api.http.opreateHttp(vm, "addSystemManage.action", datas, "添加", "");
            }
            setTimeout(function () {
                vm.$validator.errors.clear();
            }, 300)
            bool.then(function (success) {
                if (success) {
                    vm.logincheckData();
                    setTimeout(function () {
                        vm.inforAddclick();
                    }, 800);
                }
            })
        },
        //格式化部分对象键名
        cutStr(name1, name2, Obj) {
            var obj = Obj;
            for (var key in obj) {
                if (key.indexOf(name1) > -1 && key != name1) {
                    obj[name1] = obj[key];
                    delete obj[key];
                }
                if (key.indexOf(name2) > -1 && key != name2) {
                    obj[name2] = obj[key];
                    delete obj[key];
                }
            }
            return obj;
        },
        //根据按钮初始化绑定数据
        logincheckData() {
            var data;
            if (this.opreate == "update") {
                if (this.checkData.length > 0) {
                    data = this.$api.opreate.ObjectCopy(this.checkData);
                    this.addData = this.cutStr("id", "name", data[0]);
                }
            } else if (this.opreate == "add") {
                this.addData = {
                    name: "",
                    remark: "",
                    detail: ""
                };
            }
        },
        //应用系统新增
        inforAddClick(bool) {
            this.opreate = "add";
            this.inforadmin =bool;
            this.logincheckData();
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                this.inforadmin = true;
                this.opreate = "update";
                this.logincheckData();
            }
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "deleteSystemManage.action",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //功能分配
        distributeData() {
            var vm = this;
            vm.tablefunall = vm.$api.opreate.TESTcheck(vm, "功能分配");
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入用户名后搜索", {
                    icon: 0
                });
            }
        }
    },
}
</script>

<style>

</style>



